import React from 'react';
import styles from './styles.module.css';
import Header from '../Header';
import Tools from './Tools';
import Default from '../Default';
import CanvasTable from './CanvasTable';

const DrawTools = (props) => {
  const [selected, setSelected] = React.useState('1');
  const [CurrentTools, setCurrentTools] = React.useState({});
  const [data, setData] = React.useState(null);
  const local = localStorage.getItem('draw')

  const renderBody = (item, index) => {
    if (!item) return '';
    return item.isStart ? (
      <div className={styles['body']}>
        <div className={styles['toolsSide']}>
          <div>
            <Tools setCurrentTools={setCurrentTools} />
          </div>
        </div>
        <div className={styles['content']}>
          <CanvasTable currentPage={selected} imgUrl="/image/1.jpg" CurrentTools={CurrentTools} />
        </div>
      </div>
    ) : (
      <Default setData={setDataToLocal} data={data} selected={selected} />
    );
  };

  const setDataToLocal = (data) => {
    localStorage.setItem('draw', JSON.stringify(data))
    setData(data)
  }

  React.useEffect(() => {
    if (!local) {
      const drawData = [];
      drawData.push({ key: '1', bodyContent: [], isStart: true })
      localStorage.setItem('draw', JSON.stringify(drawData))
      setData(drawData);
    } else {
      setData(JSON.parse(local))
    }
  }, [local])
  return (
    <div className={styles['draw']}>
      <div className={styles['header']}>
        <Header setData={setDataToLocal} data={data} select={setSelected} />
      </div>
      {data && renderBody(data.find(x => x.key === selected))}
    </div>
  );
};

export default DrawTools;
