import React from 'react';
import { Tabs } from 'antd';
import styles from './styles.module.css';

const { TabPane } = Tabs;

const Header = ({ select, data, setData }) => {

  const [state, setState] = React.useState({
    activeKey: '1',
  })

  const newTabIndex = React.useRef(0);

  function callback(key) {
    setState(pre => ({ ...pre, activeKey: key }))
    select(key)
  }

  const onEdit = (targetKey, action) => {
    const actions = { add, remove };
    actions[action](targetKey)
  }

  const add = () => {
    const activeKey = `${newTabIndex.current = data.length > 0 ? +data[data.length - 1].key + 1 : 1}`;
    const newPanes = [...data];
    newPanes.push({ key: activeKey, bodyContent: [], isStart: false });
    setData(newPanes);
    select(activeKey);
    setState({
      activeKey,
    });
  };

  const remove = targetKey => {
    const { activeKey } = state;
    let newActiveKey = activeKey;
    let lastIndex;
    data.forEach((pane, i) => {
      if (pane.key === targetKey) {
        lastIndex = i - 1;
      }
    });
    const newdata = data.filter(pane => pane.key !== targetKey);
    if (newdata.length && newActiveKey === targetKey) {
      if (lastIndex >= 0) {
        newActiveKey = newdata[lastIndex].key;
      } else {
        newActiveKey = newdata[0].key;
      }
    }
    newTabIndex.current--;
    setData(newdata);
    select(newActiveKey)
    setState({
      activeKey: newActiveKey,
    });
  };

  const { activeKey } = state;

  return (
    <div className="d-flex">
      <div className={styles['banner']}>Draw Tools</div>
      <Tabs
        type="editable-card"
        className={styles['menu']}
        activeKey={activeKey}
        onChange={callback}
        onEdit={onEdit}
        closable={data}
      >{data?.map((item, index) => <TabPane tab={`Trang ${index + 1}`} key={`${item.key}`} />)}</Tabs>
    </div>
  );
};

Header.propTypes = {

};

export default Header;