const local = localStorage.getItem('draw');

export const save = (objects, currentPage) => {
  const data = JSON.parse(local);
  const index = data.findIndex(x => x.key === currentPage);
  data[index].bodyContent.push(...objects)
  console.log(data[index]);
  localStorage.setItem('draw', JSON.stringify(data))
}

export const getLocalData = (currentPage) => {
  const data = JSON.parse(local);
  const obj = data.find(x => x.key === currentPage).bodyContent;
  console.log("🚀 ~ file: localData.js ~ line 14 ~ getLocalData ~ obj", obj)
  return obj
}