import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { Button, Radio, Space, Tooltip } from 'antd';
import InputColor from 'react-input-color';
import styles from './styles.module.css';

const iconUrl = {
  // aix: 'icon/aix.svg',
  // autodraw: 'icon/autodraw.svg',
  bucket: 'icon/bucket.svg',
  pencil: 'icon/pencil.svg',
  type: 'icon/type.svg',
  delete: 'icon/delete.svg',
  // ham: 'icon/ham.svg',
  // linkout: 'icon/linkout.svg',
  // orientationLandscape: 'icon/orientation-landscape.svg',
  // orientationPortrait: 'icon/orientation-portrait.svg',
  // orientationSquare: 'icon/orientation-square.svg',
  pointer: 'icon/pointer.svg',
  shape: 'icon/shape.svg',
  undo: 'icon/undo.svg',
  xWhite: 'icon/x-white.svg',
  zoom: 'icon/zoom.svg',
};

function Tools({ setCurrentTools }) {
  const [SelectedTool, setSelectedTool] = React.useState();
  const [ColorCode, setColorCode] = React.useState();

  React.useEffect(() => {
    if (SelectedTool && ColorCode) {
      setCurrentTools((pre) => {
        Object.assign(pre, { SelectedTool, ColorCode });
        return { ...pre };
      });
    }
    return () => {};
  }, [ColorCode, SelectedTool, setCurrentTools]);

  const handleRadioGroup = React.useCallback((e) => {
    const value = e.target.value;
    setSelectedTool(value);
  }, []);

  const renderOptions = React.useCallback(() => {
    const arrayTool = ['pointer', 'pencil', 'type', 'bucket', 'shape', 'delete'];
    const title = ['Di chuyển', 'Vẽ', 'Văn bản', 'Tô màu', 'Hình khối', 'Xóa'];
    return arrayTool.map((item, index) => {
      const imgUrl = SelectedTool === item ? `icon/${item}-on.svg` : `icon/${item}.svg`;
      return (
        <Radio.Button key={index} value={item} className={styles.radio_button}>
          <Tooltip placement="right" title={title[index]}>
            <img style={{ width: 50 }} src={imgUrl} alt="" />
          </Tooltip>
        </Radio.Button>
      );
    });
  }, [SelectedTool]);

  const renderColorInput = React.useCallback(() => {
    return (
      <div>
        <InputColor
          initialValue="#1890FF"
          onChange={(color) => setColorCode(color)}
          placement="right"
        />
      </div>
    );
  }, []);

  return (
    <div>
      <Radio.Group
        className={styles['radio-group']}
        buttonStyle="solid"
        onChange={handleRadioGroup}
      >
        {renderOptions()}
      </Radio.Group>
      {renderColorInput()}
    </div>
  );
}

export default Tools;
