import React from 'react';
import { fabric } from 'fabric';
import { getLocalData, save } from '../../api/localData';

const HTMLCanvasElement = 'canvas-table';

const CanvasTable = ({ imgUrl, CurrentTools, currentPage, }) => {
  const [Canvas, setCanvas] = React.useState();
  const loading = React.useRef(false);

  React.useEffect(() => {
    // Set CanvasInit
    fabric.Image.fromURL(imgUrl, (img) => {
      const canvi = new fabric.Canvas(HTMLCanvasElement, {
        width: img.width,
        height: img.height,
        backgroundImage: imgUrl,
        ...initialCanvas,
      });
      setCanvas(canvi);
    });
    return () => {
      if (Canvas) Canvas.dispose();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [imgUrl]);

  React.useEffect(() => {
    if (!Canvas) return null;
    //update tool canvas
    const { SelectedTool, ColorCode } = CurrentTools;
    switch (SelectedTool) {
      case 'pencil':
        Object.assign(Canvas, { isDrawingMode: true });
        Canvas.freeDrawingBrush.width = 30;
        Canvas.freeDrawingBrush.color = ColorCode.rgba;
        break;

      default:
        Object.assign(Canvas, { ...initialCanvas });
        break;
    }
    console.log(Canvas.getObjects());
    return () => { };
  }, [Canvas, CurrentTools]);

  React.useEffect(() => {
    if (Canvas) {
      console.log("🚀 ~ file: CanvasTable.js ~ line 49 ~ React.useEffect ~ Canvas", Canvas)
      if (!loading.current) {
        // const obj = getLocalData(currentPage);
        // console.log("🚀 ~ file: CanvasTable.js ~ line 51 ~ React.useEffect ~ obj", obj)
        // Canvas.add(obj[0]);
        loading.current = true;
      } else {
        const obj = Canvas.getObjects();
        save(obj, currentPage)
      }
    }
  })

  return (
    <div>
      <button onClick={() => {
        const obj = getLocalData(currentPage);
        console.log("🚀 ~ file: CanvasTable.js ~ line 51 ~ React.useEffect ~ obj", obj)
        Canvas.add(obj[0]);
      }}>Add</button>
      <canvas id={HTMLCanvasElement} style={{ border: '1px solid black' }} />
    </div>
  );
};
export default CanvasTable;

//
const initialCanvas = {
  isDrawingMode: false,
  renderOnAddRemove: true,
};
