import React from 'react';
import PropTypes from 'prop-types';
import gif from './animation.gif';
import styles from './styles.module.css';

const Default = ({ setData, data, selected }) => {

  const newPage = () => {
    const newData = Array.from(data);
    const index = newData.findIndex(x => x.key === selected);
    newData[index].isStart = true;
    setData(newData);
  }

  return (
    <div className={styles['container']}>
      <div className={styles['img-cover']}>
        <img className={styles['img-animation']} src={gif} alt="gif" />
      </div>
      <h3 className={styles['title']}>BỘ CÔNG CỤ VẼ CƠ BẢN</h3>
      <div className={styles['functions']}>
        <div className={`${styles['button']} ${styles['import']}`}>
          <img className="mx-2" src="/image/import.png" alt="blank" width="35" height="35" />
          Thêm ảnh
        </div>
        <div onClick={newPage} className={`${styles['button']} ${styles['blank']}`}>
          <img className="mx-2" src="/image/blank-paper.png" alt="blank" width="40" height="40" />
          Trang mới
        </div>
      </div>
    </div>
  );
};

Default.propTypes = {

};

export default Default;